<template>
  <div class="about">
    <h1></h1>
    <el-form ref="forms" :rules="rules" :model="model" label-width="120px">
      <el-form-item prop="categories" label="">
        <el-select v-model="model.categories" multiple>
          <el-option
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item prop="title" label="">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item prop="body" label="">
        <vue-editor v-model="model.body" useCustomImageHandler></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" :loading="saveLoading" @click="save"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script setup>
import { reactive, ref } from 'vue'
import { VueEditor } from "vue3-editor"
const forms = ref(null)
const saveLoading = ref(false)
const rules = reactive({
  categories: [
    {
      required: true,
      message: ''
    }
  ],
  title: [
    {
      required: true,
      message: ''
    }
  ],
  body: [
    {
      required: true,
      message: ''
    }
  ]
})
const model = reactive({
  categories: [],
  title: '',
  body: ''
})

const handleSave = async () => {
  saveLoading.value = true
}

const save = () => {
  forms.value.validate((err) => {
    if (err) {
      handleSave()
    }
  })
}

</script>